import colors from '../Enums/colors';

export default {
    layout: {
        font: {color: '#ffffff'},
        paper_bgcolor: colors.backgroundGray,
        margin: {
            t: 50
        },
    },

    config: {
        modeBarButtonsToRemove: ['zoom2d', 'pan2d', 'select2d', 'zoomIn2d', 'zoomOut2d', 'autoScale2d', 'resetScale2d', 'hoverClosestGl2d', 'lasso2d','toggleSpikelines','hoverClosestCartesian', 'hoverCompareCartesian'],
        displaylogo: false
    },

    annotations: (distributor, height) => (
        [
            {
                text: `Source: ${distributor.length < 30 ? 
                    distributor : 
                    distributor.slice(0,30)} -- Provided by Simons CMAP`,
                font: {
                    color: 'white',
                    size: 10
                },
                xref: 'paper',
                yref: 'paper',
                yshift: height ? 0 - height + 160 : -290,
                showarrow: false,
            }
        ]
    )
}